<template>
  <Form ref="formInline" :model="formInline" inline>
    <FormItem prop="en">
      <Input type="text" v-model="formInline.en" placeholder="" style="width: 200px">
        <span slot="prepend"></span>
      </Input>
    </FormItem>
    <FormItem prop="ps">
      <Input type="text" v-model="formInline.ps" placeholder="" style="width: 200px">
        <span slot="prepend"></span>
      </Input>
    </FormItem>
    <Divider class="mine_diver"></Divider>
    <FormItem
      v-for="(value, k, indx) in formInline"
      v-if="k != 'en' && k != 'ps' && k != 'desc' && k != 'audio' && k != 'ctrl'"
      :key="k"
      :prop="k"
    >
      <Input type="text" v-model="formInline[k]" :placeholder="wordText[k]" style="width: 200px">
        <span slot="prepend">{{k}}.</span>
      </Input>
    </FormItem>
    <Divider class="mine_diver"></Divider>
    <FormItem class="ds_box">
      <Input v-model="formInline.desc" type="textarea" placeholder="" class="desc" />
    </FormItem>
    <br />
    <FormItem>
      <Button type="primary" @click="handleSubmit('formInline')"></Button>
    </FormItem>
  </Form>
</template>
<script>
export default {
  props: ["clear"],
  data() {
    return {
      formInline: {
        v: "", //
        n: "", //
        adj: "", //
        adv: "", //
        pron: "", //
        prep: "", //
        vi: "",
        vt: "", //
        s: "",
        conj: "",
        sc: "",
        o: "",
        oc: "",
        "aux v": "",
        art: "",
        num: "",
        u: "",
        c: "",
        pl: "",
        en: "",
        ps: "",
        desc: "",
        det: "",
        int: ""
      },
      wordText: {
        v: "",
        n: "",
        adj: "",
        adv: "",
        pron: "",
        prep: "",
        vi: "",
        vt: "",
        s: "",
        conj: "",
        sc: "",
        o: " ",
        oc: " ",
        "aux v": "",
        art: "",
        num: "",
        u: "",
        c: "",
        pl: "",
        det: "",
        int: ""
      },
      err_title: {
        en: "",
        ps: ""
      }
    };
  },
  methods: {
    handleSubmit(name) {
      //   console.log('formInline', this.formInline);
      let msg = this.handleEmpty(this.formInline, ["en", "ps"]);
      if (msg) {
        this.$Message.warning(msg);
        msg = null;
        return;
      }
      var tempNewWord = JSON.parse(JSON.stringify(this.formInline));
      tempNewWord.audio = true;
      tempNewWord.back_color = false;
      tempNewWord.ctrl = 0;
      this.$emit("addWord", tempNewWord, this.clear);
    },
    handleEmpty(ob, arr) {
      let err_msg = "";
      arr.filter((v, i) => {
        if (!ob[v]) {
          !err_msg ? (err_msg = this.err_title[v]) : null;
        }
      });

      if (err_msg) {
        return err_msg;
      } else {
        return 0;
      }
    }
  },
  watch: {
    clear: function(n) {
      if (n) {
        Object.keys(this.formInline).filter(clearI => {
          this.formInline[clearI] = "";
        });
        this.$emit("addWord", null, n);
      }
    }
  }
};
</script>
